body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Custom Properties, update these for your own design */

:root {
  --ff-primary: "Source Sans Pro", sans-serif;
  --ff-secondary: "Source Code Pro", monospace;

  --fw-reg: 300;
  --fw-bold: 900;

  --clr-light: #fff;
  --clr-dark: #303030;
  --clr-accent: #035ee7;

  --fs-h1: 3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.125rem;
  --fs-body: 1rem;

  --bs: 0.25em 0.25em 0.75em rgba(0,0,0,.25),
        0.125em 0.125em 0.25em rgba(0,0,0,.15);
}

@media (min-width: 800px) {
  :root {
    --fs-h1: 4.5rem;
    --fs-h2: 3.75rem;
    --fs-h3: 1.5rem;
    --fs-body: 1.125rem;
  }
}

/* General Styles */

body {
  background: #fff;
  background: var(--clr-light);
  color: #303030;
  color: var(--clr-dark);
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
  font-family: var(--ff-primary);
  font-size: 1rem;
  font-size: var(--fs-body);
  line-height: 1.6;
}

section {
  padding: 5em 2em;
}

img {
  display: block;
  max-width: 100%;
}

:focus {
  outline: 3px solid #035ee7;
  outline: 3px solid var(--clr-accent);
  outline-offset: 3px;
}

/* Typography */

strong { font-weight: 900; font-weight: var(--fw-bold);}

h1,
h2,
h3 {
  line-height: 1;
  margin: 0;
}

h1 { font-size: 3rem; font-size: var(--fs-h1); }
h2 { font-size: 2.25rem; font-size: var(--fs-h2); }
h3 { font-size: 1.125rem; font-size: var(--fs-h3); }

.section__title {
  margin-bottom: .25em;
}

.section__subtitle {
  margin: 0;
  font-size: 1.125rem;
  font-size: var(--fs-h3);
  color: #fff;
  color: var(--clr-light);
}


.footer {
    background: #212529;
    color: var(--clr-accent);
    text-align: center;
    padding: 2.5em 0;
    font-size: var(--fs-h3);
}

.footer a {
    color: inherit;
    text-decoration: none;
}

.footer__link { 
    font-weight: var(--fw-bold);
}

.footer__link:hover,
.social-list__link:hover {
    opacity: .7;
}

.footer__link:hover {
    text-decoration: underline;
}

.social-list {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 2em 0 0;
    padding: 0;
}

.social-list__item {
    margin: 0 .5em;
}

.social-list__link {
    padding: .3em;
}
/* My Work on Homepage */
.my-work {
    background-color: var(--clr-dark);
    color: var(--clr-light);
    text-align: center;    
}

.section__subtitle--work {
    font-weight: var(--fw-bold);
    margin-bottom: 2em;
}

.portfolio {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.portfolio__item::after {
    content: '';
    display: block;
    width: 10em;
    height: 1px;
    margin: 2em auto 2em;
    background: var(--clr-light);
    opacity: 0.25;
}


/* My Work Page */
.my-work--title {
    margin-bottom: 1.5em;
}

.my-work--subtitle {
    color: var(--clr-light);
    margin-bottom: 1.5em;
}

.my-work__page {
    text-align: center;
    background: var(--clr-dark);  
    background-repeat: repeat;
    padding-top: 2em;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
}

.my-work--title {
    color: var(--clr-light);
}

.my-work__table--items {
    background: var(--clr-light);
}

.my-work__table {                   
    display: inline-table;
    overflow: scroll;
    max-width: 50%;
}

@media (min-width: 350px) { 
    .my-work__page {
        text-align: center;
        background: var(--clr-dark);  
        background-repeat: repeat;
        padding-top: 2em;
        width: 100%;
        height: 130%;
        position: absolute;
        z-index: 2;
    }

}
.section__title--intro {
    font-weight: var(--fw-reg);
}

.section__subtitle--intro {
    background: var(--clr-accent);
    padding: .25em 1em;
    font-family: var(--ff);
    margin-bottom: 1em;
}
  
.section__title--intro strong {
    display: block;
}

.intro {
    position: relative;
}

.intro__img {
    box-shadow: var(--bs);
    }

@media (min-width: 600px) {
    .intro {
      display: grid;
      width: -webkit-min-content;
      width: min-content;
      margin: 0 auto;
      grid-column-gap: 1em;
      grid-template-areas: 
          "img title"
          "img subtitle";
          grid-template-columns: -webkit-min-content -webkit-max-content;
          grid-template-columns: min-content max-content;
    }

    .intro__img {
        grid-area: img;
        min-width: 250px;
        position: relative;
        z-index: 2;
    }

    .section__subtitle--intro {
        align-self: flex-start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
    }
}

@media (min-width: 800px) {
    .intro {
      display: grid;
      width: -webkit-min-content;
      width: min-content;
      margin: 0 auto;
      grid-column-gap: 3em;
      grid-template-areas: 
          "img title"
          "img subtitle";
          grid-template-columns: -webkit-min-content -webkit-max-content;
          grid-template-columns: min-content max-content;
    }

    .intro__img {
        grid-area: img;
        min-width: 250px;
        position: relative;
        z-index: 2;
    }

    .section__subtitle--intro {
        align-self: flex-start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
    }
}
.nav-bar__item {
    color: var(--clr-light) !important;
}
.section__subtitle--about {
  background: var(--clr-accent);
  padding: 0.25em 1em;
  font-family: var(--ff);
  margin-bottom: 1em;
}

.about-me {
  max-width: 1000px;
  margin: 0 auto;
}

.about-me__img {
  box-shadow: var(--bs);
}

.about-me__btn {
  display: flex !important;
  position: relative;
  margin-top: 3em;
  justify-content: center;
}

.about-me__page {
  text-align: center;
  background: var(--clr-dark);
  color: var(--clr-light);
  padding-top: 2em;
  width: 100%;
  height: 100%;
  position: absolute;
}

@media (min-width: 600px) {
  .about-me {
    display: grid;
    grid-template-columns: 1fr 200px;
    grid-template-rows: 2fr;
    grid-template-areas:
      "title img"
      "subtitle img"
      "text img";
    grid-column-gap: 2em;
  }

  .section__title--about {
    grid-area: title;
  }

  .section__subtitle--about {
    grid-column: 1 / -1;
    grid-row: 2;
    position: relative;
    left: -1em;
    width: calc(100% + 2em);
    padding-left: 1em;
    padding-right: calc(200px + 4em);
  }

  .about-me__img {
    grid-area: img;
    position: relative;
    z-index: 2;
  }

  .about-me__title {
    margin-bottom: 1.5em;
  }

  .about-me__description {
    inline-size: 75%;
    margin: auto;
    overflow-wrap: break-word;
  }
}

@media (min-width: 350px) {
  .about-me__title,
  .about-me__description {
    inline-size: 75%;
    margin: auto;
    overflow-wrap: break-word;
  }

  .about-me__title {
    margin-bottom: 1.5em;
  }

  .about-me__page {
    text-align: center;
    background: var(--clr-dark);
    color: var(--clr-light);
    width: 100%;
    height: 110%;
    position: absolute;
  }
}

.tech--page {
  background: var(--clr-dark);
  margin-bottom: -100em;
}

.my-technologies {
  background-color: var(--clr-dark);
  background-image: url(/static/media/technologies.cdef20c4.jpg);
  background-size: cover;
  /* below will darken background */
  background-blend-mode: multiply;
  color: var(--clr-light);
  text-align: center;
}

.section__title--technology {
  color: var(--clr-light);
  position: relative;
}

.section__title--technology::after {
  content: "";
  display: block;
  width: 2em;
  height: 1px;
  margin: 0.5em auto 1em;
  background: var(--clr-light);
  opacity: 0.25;
}

.technology-item {
  display: flex;
  max-width: 400px;
  max-height: 300px;
  margin: 0 auto;
  margin-bottom: 4em;
  justify-content: center;
  vertical-align: middle;
}

.tech__img {
  display: block;
  max-height: 15em;
  max-width: 15em;
  width: 20%;
  margin-left: auto;
  margin-right: auto;
  color: var(--clr-light);
}

.tech__name {
  color: var(--clr-light);
  font-size: x-large;
  text-align: center;
  margin-top: 1em;
  padding-bottom: 3em;
}

.tech__list {
  display: grid !important;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.tech--page--title,
.tech--page--subtitle {
  display: block;
  color: var(--clr-light);
  text-align: center;
  inline-size: 85%;
  margin: auto;
}

.tech--page--title {
  padding-top: 1.5em;
}

.tech--page--subtitle {
  padding-top: 1.25em;
  padding-bottom: 2em;
}

.tech--page__language {
  display: block;
  color: var(--clr-light);
  text-align: start;
  inline-size: 85%;
  margin-top: 2em;
  margin-left: 2em;
}

.tech--img__programming,
.tech--img__webdev,
.tech--img__db {
  display: flex;
  max-width: 300px;
  max-height: 200px;
  margin: 2em;
  vertical-align: middle;
}

@media (min-width: 350px) {
  .tech__items {
    display: block;
    margin: 2em auto;
  }

  .tech--img__programming,
  .tech--img__webdev,
  .tech--img__db {
      display: block;
      margin: 2em auto;
  }
}

@media (max-width: 810px) {
    .tech__items {
        display: block;
        margin: 2em auto;
      }

    .tech--page__language {
        text-align: center;
    }
}

@media (min-width: 811px) {
  .technologies {
    display: flex;
    max-width: 1000px;
    margin: 0 auto;
  }

  .technology-item + .technology-item {
    padding-left: 2em;
  }

  .tech__items {
    display: flex;
    max-width: 1000px;
    margin-left: 3em;
  }
}

.btn {
    display: inline-block;
    padding: .5em 2.5em;
    background: var(--clr-accent);
    color: var(--clr-dark);
    text-decoration: none;
    cursor: pointer;
    font-size: .8rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: var(--fw-bold);
    transition: -webkit-transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
  }
  
  .btn:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }


